*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@media screen and (max-width: 720px) {
    .alpha-wrapper{
        padding-top: 0.5rem;
        font-size: 0.5rem;
        header{
            padding: 0;
        }
    }   
    .name-tag{
        width: auto;
        margin-top: -3rem;
        padding: 0;
        text-align: center;
        img{
            width: 1rem;
            height: 1rem;
            margin: 0;
        }
    }
    .social-links{
        position: static;
        display: block;
        margin-left: auto;
        margin-right: auto;
        height: auto;
        bottom: 0;
        a{
        img{
            width: 1.5rem;
            height: 1.5rem;
        }}
    }
    header{
        flex-direction: column;
        justify-content: space-evenly;
        gap: 0rem;
        //height: 10rem;
        padding: 0;
        border: 2px solid white;
    }
    #header-img{
        margin-left: 0;
        width: 1rem;
        height: 1rem;
    }
    
    @keyframes spin-out {
        from{
            width: 0.1rem;
            height: 0.1rem;
            transform: rotate(-360deg);
        }
        to{
            width: 5rem;
            height: 5rem;
            transform: rotate(0deg);
        }
    }
}